@font-face {
  font-family: Monoton;
  src: url(../../Monoton-Regular.ttf);
}

#projects {
  margin: auto;
  width: 90%;
  padding: 10px;
  display: flex;
}

figure.snip0058 {
  font-family: "Raleway", Arial, sans-serif;
  color: #fff;
  position: relative;
  overflow: hidden;
  margin: 10px;
  max-width: 540px;
  background-color: #5c565685;
  border-radius: 25px;
  padding: 15px 17px;
}

figure.snip0058 > img {
  opacity: 1;
  width: 100%;
  filter: blur(0px);
  -webkit-transition: all 0.35s;
  transition: all 0.35s;
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
}

figure.snip0058 figcaption {
  left: 30px;
  margin-bottom: -50px;
  position: absolute;
  right: 30px;
  text-align: left;
  bottom: 100%;
  -webkit-transition: bottom 0.35s;
  transition: bottom 0.35s;
}

figure.snip0058 figcaption h2 {
  width: 100%;
  text-align: left;
  border-radius: 0 0 25px 25px;
  margin: 0;
  padding: 15px 20px;
  font-size: 1.1em;
  bottom: 0;
  font-weight: 300;
  text-transform: uppercase;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  background-color: #000000;
  position: relative;
  z-index: 1;
}

figure.snip0058 figcaption h2 span {
  font-weight: 800;
}

figure.snip0058 figcaption p {
  position: relative;
  background-color: rgba(255, 255, 255, 0.9);
  color: #000;
  margin: 0;
  font-size: 0.9em;
  min-height: 220px;
}

figure.snip0058 figcaption p span {
  padding: 15px 20px;
  position: absolute;
  bottom: 0;
}

figure.snip0058 figcaption .icons {
  padding: 5px;
  position: absolute;
  right: 0;
  bottom: 0;
}

figure.snip0058 figcaption .icons .github-icon, 
figure.snip0058 figcaption .icons .link-icon {
  font-size: 23px;
  padding: 4px;
  color: #ffffff;
  opacity: 1;
  position: relative;
  bottom: -90px;
  opacity: 0;
  -webkit-transition: bottom 0.35s, opacity 0.35s;
  transition: bottom 0.35s, opacity 0.35s;
}

figure.snip0058:after {
  position: absolute;
  display: block;
  bottom: -15px;
  height: 15px;
  right: 30px;
  left: 30px;
  border-radius: 25px 25px 0 0;
  background-color: #000000;
  z-index: 2;
  content: "";
  -webkit-transition: bottom 0.35s;
  transition: bottom 0.35s;
}

figure.snip0058 a {
  opacity: 0.7;
}

figure.snip0058 a:hover {
  opacity: 1;
}

figure.snip0058:hover > img,
figure.snip0058.hover > img {
  opacity: 0.5;
  filter: blur(5px);
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

figure.snip0058:hover figcaption,
figure.snip0058.hover figcaption {
  bottom: 115px;
}

figure.snip0058:hover figcaption .icons .github-icon ,
figure.snip0058.hover figcaption .icons .github-icon,  
figure.snip0058:hover figcaption .icons .link-icon,
figure.snip0058.hover figcaption .icons .link-icon
{
  opacity: 1;
  bottom: -41px;
}


figure.snip0058:hover figcaption::before,
figure.snip0058.hover figcaption::before {
  opacity: 0.8;
}

figure.snip0058:hover:after,
figure.snip0058.hover:after {
  bottom: 0;
}

#projects-h2 {
  display: flex;
  justify-content: center;
  color: #05d9e8;
  font-size: 60px;
  font-family: Monoton;
  padding: 30px 0px;
}